<template>
    <div >
       <ul class="footer" :style="{background: bg}">
           <li>
               <router-link to="/movie/movieList" >书架</router-link>               
           </li>
           <li>
               <router-link to="/music" >排行榜</router-link>               
           </li>
           <li>
               <router-link to="/book" >书库</router-link>               
           </li>
           <li>
               <router-link to="/photo" >发现</router-link>               
           </li>
       </ul>
    </div>
</template>

<script>
export default {
    props: ['bg']
};
</script>

<style scoped>
.footer{
    width: 100%;
    height: 1rem;
    background:#fff;
    position: fixed;
    bottom:0;
    line-height: 1rem;
    display:flex;
    justify-content: space-evenly;
    border-top: 1px solid #ccc;
}

.footer li a{ color:#000; outline:none;}
.footer li a.router-link-active{color: #24B8F4; } /*被点中的为白色*/
</style>




