<template>
    <div class="detail">
      <!--    <h2>MovieDetail: {{$route.params.movieId}}</h2> --><!--url1公网api-->
        <div class="detail-content">
            <div class="detail-img">
                <img :src="movieDetail.img" />
            </div>
            <div class="detail-info">
                <h2 v-text="movieDetail.nm"></h2>
                <p v-text="'作者：'+movieDetail.star"></p>
                <p>{{movieDetail.cat}}</p>
                <p>{{movieDetail.src}} : {{movieDetail.dur}}</p>
                <p style="color:red">{{movieDetail.rt}}</p>
            </div>
        </div>
       <!--  <hr />
        <div>
            <h3>主演</h3>
            <p>{{movieDetail.star}}</p>
        </div> -->
        <hr />
        <div>
            <h3>简介</h3>
            <p>{{movieDetail.showDate}}</p>
        </div>

    </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){//此处必须使用data()方法定义数据
        return {
            movieDetail: {},
        }
    },
    mounted() {
       // let url1 = API_PROXY + "http://m.maoyan.com/movie/" + this.$route.params.movieId + ".json"
        let url2 = "/static/data/moviedata.json"
        let i =this.$route.params.movieId;
        /* let url2 = '/static/data/moviedetail'+ this.$route.params.movieId  +'.json' */

    //moviedetail3389.json;

        Axios.get(url2).then(res => {
            this.movieDetail = res.data.data.movies[i];
            console.log(this.movieDetail);
        }).catch();
    }
}
</script>

<style scoped>

h2{font-size: 0.4rem;}
.detail{ margin: 1rem 0; padding:0.2rem; }
.detail-content{ display:flex; margin-bottom: 0.2rem;}
.detail-img{ flex: 1 1 0; margin-right: 0.2rem;}
.detail-info{flex: 2 1 0;color: #000;}
.detail h3{ text-align: center;}
</style>


