

<template>
   <div>
  
       <ul class="list">
           <li v-for="photo in photoList" :key="photo.id">
               <router-link :to="'/photo/photoDetail/'+photo.id">
                   <img :src="photo.bg" />
               </router-link>           
           </li>
       </ul>
   </div> 
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            photoList: []
        }
    },
    mounted() {
      let url = "/static/data/musiclist.json" 

       Axios.get(url).then(res => {
           this.photoList = res.data.albums;
       }).catch(); 
    }
}
</script>
<style scoped>
.list{ margin:0.8rem 0; display:flex; flex-wrap: wrap; }
.list li{width:50%;}
.list li img{width:100%;}
</style>


