<template>
    <div>
        <common-header bg="rgb(023A0E6)">
            <div slot="title">排行榜</div>
            
        </common-header>
         <div id="top">
             <div class="top-main">
                <ul>
                    <li class="on"><a href="">男生</a></li>
                    <li><a href="#">女生</a></li>
                     <li></li>
                      <li></li>
                       <li></li>
                        <li></li>
                        
                </ul>
             
                <ul>
                    <li class="on"><a href="#">热卖</a></li>
                    <li><a href="#">完结</a></li>
                    <li><a href="#">收藏</a></li>
                    <li><a href="#">推荐</a></li>
                    <li><a href="#">新书</a></li>
                    <li><a href="#">评分</a></li>
                </ul>
             
                <ul>
                    <li class="on"><a href="#">周榜</a></li>
                    <li><a href="#">月榜</a></li>
                    <li><a href="#">总部</a></li>
                     <li></li>
                      <li></li>
                       <li></li>
                </ul>
                
            </div>
            </div> 

            <div class="div2">
            <ul class="book-list">
                <li class="book" v-for="book in bookList" :key="book.id">
                    
                <div class="book-img"><img :src="book.book_cover" alt=""></div>
                <div class="book-info">
                    
                         <p>{{book.bookname}}</p>
                         <p style="float: right;" b>{{book.discountNum+"分"}}</p>
                     
                     
                     <p>{{book.author_name}}</p> 
                     <P>{{book.book_info}}</P>
                    
                </div>
              </li>
               
               
            </ul>
        </div> 
        <router-view></router-view>
        <common-footer bg="rgb(23A0E6)"></common-footer>
    </div>
</template>

<script>
import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'
import Axios from "axios"; 

export default {
    data(){
        return{
            bookList:[],
        }
    },
    components: {
        CommonHeader,
        CommonFooter,
    },
    methods:{
        loadData(){
        let url1="/static/data/bookdata2.json";
            Axios.get(url1).then((res)=>{
               this.bookList = res.data.data;
               console.log(this.bookList)
            }).catch(()=>{
        });
        },
         bookL(){ 
       let main=document.getElementById('top').getElementsByTagName('ul')[0];
       var oLi=document.getElementById("top").getElementsByTagName("ul")[0].getElementsByTagName("li");
    
     for(var i=0;i<oLi.length;i++){
         (function(index){
             oLi[index].onclick=function(){
                 for(var j=0;j<oLi.length;j++){
                     oLi[j].className="";
                 }
                 oLi[index].className="on";
            }
       })(i); } 
    },
       
},
 mounted () {
        this.loadData();
        this.bookL();
    },
     
}
</script>

<style scoped>
li{list-style: none;}
a{text-decoration: none;}
span{font-size: 0.4rem}
#top{margin-top: 1.1rem;position: relative;height: 2.1rem;border-bottom: 5px solid #ccc;}
#top .top-main{width: 100%;overflow: auto;background: #fff;}
#top .top-main a{color: #515150;}
#top .top-main::-webkit-scrollbar{display:none;}
#top ul{width: 100%;height: 40px;text-align: center;display: flex;justify-content:space-evenly;}

#top ul li{width: 30px;height: 28px;}
#top li.on{width: 0.8rem;height: 0.3rem;background: #24B8F4;line-height: 0.3rem;border-radius: 5px;}
#top li.on a{font-weight: bold;color: #fff;}


.div2{margin: 0.1rem 0.2rem;}

.div2 ul li{display: flex;justify-content: space-around;}
.book-img{width: 7rem;height: 3rem;}
.book-info P{margin-left: 0.3rem;}
.book-info P:nth-child(1){color: #000;font-weight: bold;}
.book-info P:nth-child(2){color: rgb(243, 24, 24);font-size: 0.3rem}
.book-info P:nth-child(4){color: #000;font-size: 0.17rem;}

</style>




