<template>
    <div>
        <common-header bg="rgb(D43C33)">
            <span slot="title">书库</span>
        </common-header>
         <div id="top">
             <div class="top-main">
                <ul>
                    <li class="on"><a href="">首页</a></li>
                    <li><a href="#">排行 </a></li>
                    <li><a href="#">书单</a></li>
                  

                </ul>
            </div>
            </div> 
        <swipe class="my-swipe">
            <swipe-item class="slide1"><h1 style="font-size:80px"><img src="/static/img/q1.jpg" alt=""></h1></swipe-item>
            <swipe-item class="slide1"><img src="/static/img/q2.jpg" alt=""></swipe-item>
            <swipe-item class="slide1"><img src="/static/img/q3.jpg" alt=""></swipe-item>
            <swipe-item class="slide1"><img src="/static/img/q4.jpg" alt=""></swipe-item>
        </swipe>
         <footer id="foof">
                    <ul>
                        <li class="on"><a href="#"><img class="img" src="/static/img/my5.jpg">推荐</a></li>
                        <li><a href="#"><img class="img" src="/static/img/my3.jpg">收藏</a></li>
                        <li><a href="#"><img class="img" src="/static/img/my2.jpg">分类</a></li>
                        <li><a href="#"><img class="img" src="/static/img/my4.jpg">完结</a></li>
                        <li><a href="#"><img class="img" src="/static/img/my1.jpg">更新</a></li>
                    </ul>
                </footer>
                <div class="foot">
                        <ul>
                            <li><a href="">重磅推荐</a></li>
                            <li></li>
                            <li>全部></li>
                        </ul>
                    </div> 
        <div class="div1">
            <ul class="book-list">
                <li class="book" v-for="book in bookList" :key="book.id">
                    
                <div class="book-img"><img :src="book.book_cover" alt=""></div>
                <div class="book-info">
                     
                     <p>{{book.bookname}}</p> 
                     <p>{{book.author_name}}</p> 
                </div>
              </li>
               
               
            </ul>
        </div>
         <div class="foot1">
                        <ul>
                            <li><a href="">热门加载</a></li>
                            <li></li>
                            <li>全部></li>
                        </ul>
                    </div> 
        <div class="div2">
            <ul class="book-list">
                <li class="book" v-for="book in bookList" :key="book.id">
                    
                <div class="book-img"><img :src="book.book_cover" alt=""></div>
                <div class="book-info">
                     
                     <p>{{book.bookname}}</p> 
                     <p>{{book.author_name}}</p> 
                </div>
              </li>
               
               
            </ul>
        </div> 


            <div class="kong"></div>
      <common-footer bg="rgb()"></common-footer> 
        
    </div>
</template>

<script>
import CommonHeader from '../common/Header'
 import CommonFooter from '../common/Footer' 
 import { Swipe, SwipeItem } from 'vue-swipe';
import 'vue-swipe/dist/vue-swipe.css';  
import Axios from "axios"; 
/**
  一.import 文件中没有export,不能使用from:
 1. import 'reset.css' 相当于: <link rel="stylesheet" href="reset.css">
 2. import 'jqery.js' 相当于:  <script src="jquery.js">
  二. import 与 require区别:
  import "文件.扩展名": 是ES6引入模块的方法, vue中建议使用import
  require("文件.扩展名"): 是nodeJs引入模块的方法, nodeJs是平台,是环境. 
 */

export default {
      data(){
        return{
            bookList:[],
        }
    },
     components: {
        CommonHeader,
        CommonFooter, 
        'swipe': Swipe,
        'swipe-item': SwipeItem 
} ,

   methods:{
        loadData(){
        let url1="/static/data/bookdata.json";
            Axios.get(url1).then((res)=>{
               this.bookList = res.data.data;
               console.log(this.bookList)
            }).catch(()=>{
        });
        },
       
     bookL(){ let main=document.getElementById('top').getElementsByTagName('ul')[0];
       var oLi=document.getElementById("top").getElementsByTagName("ul")[0].getElementsByTagName("li");
    
     for(var i=0;i<oLi.length;i++){
         (function(index){
             oLi[index].onclick=function(){
                 for(var j=0;j<oLi.length;j++){
                     oLi[j].className="";
                 }
                 oLi[index].className="on";
            }
       })(i); } 
    },
    },
       mounted () {
        this.loadData();
       this.bookL(); 
//     },
     
 },
}
</script>

<style scoped>

li{list-style: none;}
a{text-decoration: none;}
span{font-size: 0.4rem}
#top{margin-top: 1.1rem;position: relative;}
#top .top-main{width: 100%;overflow: auto;background: #fff;}
#top .top-main a{color: #515150;}
#top .top-main::-webkit-scrollbar{display:none;}
#top ul{width: 100%;height: 40px;text-align: center;display: flex;justify-content: space-around;}
#top ul li{width: 30px;height: 25px;}
#top li.on{border-bottom: 2px solid #24B8F4;}
#top li.on a{font-weight: bold;color: #24B8F4;}

.foot{width: 90%;margin: 0 auto;line-height: 0.5rem;}
.foot a{font-size: 0.3rem;font-weight: bold;color: #000;}
.foot ul{display:flex;justify-content: space-between;}
.book-img{width: 1.2rem;}
.book p:first-child{color: #000;font-size: 0.15rem}
.book p:last-child{color: #ccc;font-size: 0.1rem}
.div1{height: 6.5rem;border-bottom: 5px solid #ccc;}
.div1 ul{display:flex; flex-wrap: wrap;margin-left: 0.35rem; }
.div1 li{width: 32%;}


.foot1{width: 90%;margin: 0 auto;line-height: 0.5rem;}
.foot1 a{font-size: 0.3rem;font-weight: bold;color: #000;}
.foot1 ul{display:flex;justify-content: space-between;}
.book-img{width: 1.8rem;}
.book p:first-child{color: #000;font-size: 0.15rem}
.book p:last-child{color: #ccc;font-size: 0.1rem}
.div2{height: 6.5rem;border-bottom: 5px solid #ccc;}
.div2 ul{display:flex; flex-wrap: wrap;margin-left: 0.35rem; }
.div2 li{width: 32%;}




 .my-swipe {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
} 

.slide1 {
  width: 100%;

  color: #fff;
  z-index: -1;
} 


#foof{width: 100%;height: 80px;background: #fff;padding-top:10px;border-top:1px solid #E2E2E2;z-index: 100;border-bottom: 5px solid #ccc}
#foof img{width: 80px;height:35px;margin-bottom: -4px;}
#foof ul{width: 100%;display:flex;justify-content: space-evenly;}
#foof ul li{width: 40px;text-align: center;}
#foof ul li a{color: #333;font-size: 10px;font: none;}
.kong{height: 1rem;}

</style>




